import { DialogActions, Grid, Select } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import DialogContent from '@material-ui/core/DialogContent'
import InputMask from 'react-input-mask'
import { ValidatorForm, TextValidator} from 'react-material-ui-form-validator';
import TextField from '@material-ui/core/TextField'
import withStyles from '@material-ui/core/styles/withStyles'
import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'

import styles from '../../../resources/theme/users'
import EnhancedComponent from '../../components/EnhancedComponent'

class SupplierForm extends EnhancedComponent {

  state = {
    dirty: false,
    fields: {
      CEP: '',
      CNPJ: '',
      bairro: '',
      city: '',
      complement: '',
      description: '',
      number: '',
      phone: '',
      state: '',
      street: '',
    },
  }

  handleClose = () => this.props.onClose && this.props.onClose()

  componentWillReceiveProps(nextProps) {
    if (!this.state.dirty && nextProps.data) {
      this.setState({
        fields: {
          ...nextProps.data,
        },
      })
    }
  }

  onSubmit = e => {
    e.preventDefault()
    this.props.onSubmit && this.props.onSubmit(this.state.fields)
  }

  render() {
    const { classes, roles } = this.props
    return (
      <ValidatorForm
                ref="form"
                onSubmit={this.onSubmit}
                onError={errors => console.log(errors)}
        >
        <DialogContent>
          <Grid container spacing={16}>
            <Grid item xs>
              <TextValidator
                onChange={this.onChange('description')}
                label="Descrição"
                margin="dense"
                variant="outlined"
                fullWidth
                value={this.state.fields.description}
                validators={['required']}
                errorMessages={['Campo Obrigatório']}
              />
            </Grid>

            <Grid item xs>
              <InputMask
                    mask={'(99) 99999-9999'}
                    value={this.state.fields.phone}
                    onChange={this.onChange('phone')}
                  >
                    {() => (
                      <TextField
                        label="Telefone"
                        fullWidth
                        variant="outlined"
                        margin="dense"
                      />
                    )}
                  </InputMask>
            </Grid>

            <Grid item xs>
               <InputMask
                    mask={'99.999.999/9999-99'}
                    value={this.state.fields.CNPJ}
                    onChange={this.onChange('CNPJ')}
                  >
                    {() => (
                      <TextField
                        label="CNPJ"
                        fullWidth
                        variant="outlined"
                        margin="dense"
                      />
                    )}
                  </InputMask>
            </Grid>

          </Grid>

          <Grid container spacing={16}>
            <Grid item xs>
              <InputMask
                    mask={'99.999-999'}
                    value={this.state.fields.CEP}
                    onChange={this.onChange('CEP')}
                  >
                    {() => (
                      <TextField
                        label="CEP"
                        fullWidth
                        variant="outlined"
                        margin="dense"
                      />
                    )}
                  </InputMask>
            </Grid>

            <Grid item xs>
              <TextField
                onChange={this.onChange('state')}
                label="Estado"
                margin="dense"
                variant="outlined"
                required
                fullWidth
                value={this.state.fields.state}
              />
            </Grid>

            <Grid item xs>
              <TextField
                onChange={this.onChange('city')}
                label="Cidade"
                margin="dense"
                variant="outlined"
                required
                fullWidth
                value={this.state.fields.city}
              />
            </Grid>

            <Grid item>
              {/* <FormControl className={classes.formControl}>
                <InputLabel>Tipo de Usuário</InputLabel>
                <Select
                  onChange={this.onChange('role_id')}
                  value={this.state.fields.role_id}
                >
                  <MenuItem>Selecione...</MenuItem>
                  {roles.map(role => (
                    <MenuItem key={role.id} value={role.id}>
                      {role.value}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl> */}
            </Grid>

            <Grid container spacing={16}>
                <Grid item xs>
                  <TextField
                    onChange={this.onChange('bairro')}
                    label="Bairro"
                    margin="dense"
                    variant="outlined"
                    required
                    fullWidth
                    value={this.state.fields.bairro}
                  />
                </Grid>

                <Grid item xs>
                  <TextField
                    onChange={this.onChange('street')}
                    label="Rua"
                    margin="dense"
                    variant="outlined"
                    required
                    fullWidth
                    value={this.state.fields.street}
                  />
                </Grid>

                <Grid item xs>
                  <TextField
                    onChange={this.onChange('number')}
                    label="Número"
                    margin="dense"
                    variant="outlined"
                    required
                    fullWidth
                    value={this.state.fields.number}
                  />
                </Grid>
            </Grid>

            <Grid container spacing={16}>
               <Grid item xs={4}>
                  <TextField
                    onChange={this.onChange('complement')}
                    label="Complemento"
                    margin="dense"
                    variant="outlined"
                    fullWidth
                    value={this.state.fields.complement}
                  />
               </Grid> 
            </Grid> 

          </Grid>
        </DialogContent>
        <DialogActions>
          <Button color="primary" type="submit">
            Salvar
          </Button>
          <Button onClick={this.handleClose}>Cancelar</Button>
        </DialogActions>
        </ValidatorForm>
    )
  }
}

SupplierForm.propTypes = {
  classes: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  //roles: state.user.roles || [],
})

export default connect(mapStateToProps)(withStyles(styles)(SupplierForm))
